  background: -webkit-linear-gradient(#50C1E9, #3BAEDA);
  background: linear-gradient(#50C1E9, #3BAEDA);
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
}

.Home .container {
  width: 80%;
}
