.login {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  padding: 20px 0;
  color: #434A54;
}

.login form {
  padding: 20px 0;
  display: flex;
  flex-flow: column nowrap;
}

.login form .input {
  width: 250px;
  margin: 5px 0;
  border-radius: 20px;
  background-color: white;
  padding: 5px 15px;
