.encabezado {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
}

.encabezado .card-container {
  width: 100%;
}

.encabezado .card-container .card {
  width: 100%;
  border-radius: 0;
  border-top: 1px solid #F1F2F3;
  box-shadow: 0px 3px 1px 0px rgba(0, 0, 0, 0.3);
  pointer-events: none;
  margin: 0;
}

.encabezado-categories {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 15px 30px;
}

.encabezado-categories h2 {
  margin-right: auto;
  color: #434A54;
}

.encabezado-categories .back-button {
  background: none;
  border: none;
  outline: none;
  align-items: center;
  padding: 0;
  width: 30px;
  height: 30px;
  margin-right: auto;
}

.encabezado-categories .back-button svg {
  width: 100%;
  height: 100%;
  cursor: pointer;
  color: #434A54;
}

.encabezado form {
  grid-column: 1 / -1;
  display: flex;
  position: relative;
  background: #656D78;
  padding: 10px 30px;
  align-items: center;
  width: 100%;
}

.encabezado form button {
  background: none;
  outline: none;
  border: none;
}

.encabezado form button svg {
  color: #CCD0D9;
  font-size: 1.8em;
}

.encabezado form input {
  width: 100%;
  outline: none;
  background: none;
  border: none;
  font-size: 1.6em;
  color: #3F4752;
  padding: 0 15px;
}

.encabezado form input::-webkit-input-placeholder {
  color: #F1F2F3;
}

.encabezado form input::-ms-input-placeholder {
  color: #F1F2F3;
}

.encabezado form input::placeholder {
  color: #F1F2F3;
}
