.card {
  border-radius: 10px;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.8);
  padding: 5px 10px;
  margin: 0 20px 20px 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: white;
  color: #434A54;
  cursor: pointer;
  -webkit-transition: .2s;
  transition: .2s;
}

.card:hover {
  background: #37BD9C;
  color: white;
}

.card svg {
  margin-right: auto;
  font-size: 40px;
}

.card h3 {
  margin-right: auto;
  font-size: 1.6em;
}

.card-detail {
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.card-detail.green svg {
  color: #37BD9C;
}

.card-detail:hover {
  background-color: white;
  color: #434A54;
}

.card-detail div {
  width: 100%;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  justify-content: center;
  font-size: 1.3em;
  padding: 5px;
  position: relative;
}

.card-detail div h4 {
  color: #95989A;
}

.card-detail div svg {
  font-size: inherit;
  right: 0;
  position: absolute;
  color: #95989A;
  cursor: pointer;
  pointer-events: auto;
}

.card-detail div svg:hover {
  color: #434A54;
}

.card-detail .separador {
  border-bottom: 1px solid #95989A;
  width: 100%;
}

.card-detail input {
  margin: 0;
  padding: 5px;
  background: transparent;
  border: none;
  outline: none;
  font-size: inherit;
  font-family: inherit;
  color: #434A54;
  font-weight: bold;
  text-align: center;
  width: 60%;
}

.card-detail input.edit {
  border: 1px solid grey;
}

@media only screen and (min-width: 768px) {
  .card-detail input {
    width: 40%;
  }
}
/*# sourceMappingURL=card.css.map */
